import * as React from "react";
import {
  createEditorWithToolbar,
  enableReadOnlyMode
} from "./roosterwrapper/roosterwrapper";
import { Editor as RoosterJsEditor } from "roosterjs-editor-core/lib/index";
import { IEditor } from "roosterjs-editor-types/lib/index";
import RoosterPlugin from "./EditorPlugin";
import {
  TableResize,
  Watermark,
  HyperLink,
  Paste,
  ContentEdit,
  ImageEdit,
} from "roosterjs-editor-plugins";
// import { rteLabelProps } from "../../shared/styles/Container.styles";
// import { Text } from "office-ui-fabric-react";
// import { IConnectEditorProps } from '../../shared/interfaces/commonProps';

interface ConnectEditorStateProps {
  editorProps: any;
}

export default class ConnectEditor extends React.Component<
  any,
  ConnectEditorStateProps
> {
  private editor!: RoosterJsEditor | IEditor | undefined;
  private content: string | undefined;

  constructor(props: any) {
    super(props);
    this.content = props.content;
    this.state = {
      editorProps: props
    };
  }

  componentDidMount() {
    this.initEditor();
  }

  componentWillUnmount() {
    this.editor?.dispose();
  }

  componentDidUpdate(props: any) {
    if (this.state.editorProps.readOnly === false && props.readOnly === true) {
      this.setState({
        editorProps: props
      });
      const formId = props.id?.split("editor-");
      enableReadOnlyMode(formId[1]);
    }
  }

  setContent(content: string) {
    this.editor?.setContent(content);
  }

  getContent() {
    //if the content only contains html tags and no user text, then return empty string.
    return this.editor?.getContent()?.replace(/<[^>]+>/g, "") !== ""
      ? this.editor?.getContent()
      : "";
  }

  initEditor() {
    const toolbarOptions = {
      heading: 0,
      fontFamily: 1,
      fontSize: 1,
      underline: 1,
      bold: 1,
      image: 1,
      italic: 1,
      undo: 1,
      redo: 1,
      numbering: 1,
      bullets: 1,
      rightIndex: 1,
      leftIndent: 1,
      hyperlink: 1,
      removeHyperlink: 1,
      table: 1,
      textColor: 1,
      backgroundColor: 1,
      leftAlign: 1,
      centerAlign: 1,
      rightAlign: 1,
      justify: 1,
      expansion: 1,
      clearFormatting: 1,
      strikeThrough: 1,
    };
    const onChangePlugin = new RoosterPlugin(
      this.props.id,
      this.props.onChange
    );

    const options = {
      toolbarOptions: toolbarOptions,
      plugins: [
        new HyperLink(),
        new Paste(),
        new ContentEdit(),
        new ImageEdit(),
        new Watermark(this.props.placeholder, {
          fontSize: "14px",
          textColor: "#737373"
        }),
        new TableResize(),
        onChangePlugin
      ],
      readOnly: this.props.readOnly,
      initialContent: this.content ? this.content : ""
    };

    this.editor = createEditorWithToolbar(this.props.id, options);
  }

  private onBlur = (ev: React.FocusEvent<HTMLDivElement>) => {
    if (this.props.onBlur) {
      this.props.onBlur(ev);
    }
  };

  render() {
    return (
      <>
        {/* {this.props.label && <Text>{this.props.label}</Text>} */}
        <div
          className={
            this.props.errorMessage
              ? this.props.className + " error"
              : this.props.readOnly
              ? this.props.className + " read-only"
              : this.props.className
          }
          id={"container-" + this.props.id}
          onBlur={this.onBlur}
        >
          <div id={this.props.id} className="rooster-sub-div" />
        </div>
        {this.props.errorMessage && (
          <div role="alert">
            <p className="rooster-errormessage">
              <span data-automation-id="error-message">
                {this.props.errorMessage}
              </span>
            </p>
          </div>
        )}
      </>
    );
  }
}
