import { EditorPlugin, IEditor, PluginEvent } from 'roosterjs-editor-types';

class RoosterPlugin implements EditorPlugin {
    id: string;
    editor!: IEditor;
    handleChange: any;

    constructor(id: string, handleChange: any) {
        this.handleChange = handleChange;
        this.id = id;
    }

    getName() {
        return 'OnChangePlugin';
    }

    initialize(editor: IEditor) {
        this.editor = editor;
    }

    getContent() {
        return this.editor.getContent();
    }

    setContent(content: string) {
        this.editor.setContent(content);
    }

    dispose() {}

    onPluginEvent(event: PluginEvent) {
        switch (event.eventType) {
            case 3:
                this.handleChange(this.getContent());
                break;
            case 7:
                this.handleChange();
                break;
            default:
                break;
        }
    }
}

export default RoosterPlugin;
