const sampleContent = '<p><b>Image Test:</b></p><p><u><img src="data:image/png;base64," style="width: initial; height: initial; max-width: 100%;"><br></u></p><p><u><br></u></p><p><u>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Ab nisi eaque rem iste facilis, reiciendis error? Sit quis delectus, quibusdam, voluptate reiciendis dolore architecto consectetur necessitatibus expedita, autem doloremque culpa.</u><br>\nAssumenda ab voluptatem laboriosam sunt explicabo laborum ratione voluptates expedita obcaecati blanditiis numquam inventore, deserunt ex a nobis qui perspiciatis cupiditate dolore incidunt quae ipsam. Labore, consequatur? Corporis, facilis voluptas!<br>\nMinus obcaecati, ratione accusamus tempore sit excepturi et, nobis saepe rerum autem ab impedit! Nam provident laborum explicabo est illo temporibus nisi vitae ipsum amet voluptatibus minima, ex dicta sint.<br>\nVero saepe architecto quam optio voluptatem non quaerat cupiditate, ab temporibus aperiam nostrum necessitatibus corrupti beatae porro velit harum? Corporis a minima molestias quis minus tempora et quidem accusamus beatae.<br>\n<strong>Laudantium facere obcaecati recusandae, magni necessitatibus officia consequuntur modi. Doloremque modi nam voluptates quisquam aspernatur consectetur incidunt nostrum illo animi cumque ab numquam, dolor excepturi quia similique repellat in laudantium.</strong></p>\n\n<p> </p>\n\n<p><br>\n<strong>Quisquam temporibus explicabo eius! Adipisci doloribus, sunt facilis aperiam repudiandae animi numquam dolores est esse ullam? Asperiores minus, quia cupidit</strong>ate, blanditiis libero veritatis, itaque mollitia quaerat omnis nostrum necessitatibus distinctio!<br>\nExpedita, laudantium? Neque soluta, deserunt rerum recusandae molestiae, cum excepturi ad repellat minima doloribus unde molestias nobis praesentium repudiandae voluptatibus consectetur, beatae ipsum voluptatem assumenda illo provident veniam dolorem! Vel.<br>\nConsequatur at accusantium voluptates officiis molestias perspiciatis amet possimus iste eius distinctio ex nobis quaerat aperiam porro similique rem sit velit doloribus quisquam voluptatum quae, odit corrupti nam? Corporis, dicta.<br>\nEos excepturi odio ullam quis molestias in illum, at consequatur animi suscipit autem cum quam dolor optio perferendis <em>perspiciatis quia aliquid</em> minus explicabo nemo aliquam odit ab! Quia, reiciendis dolore!<br>\nMolestiae veniam autem fugiat consequatur, sequi doloribus error possimus, praesentium quis placeat, optio officia dolore eum tenetur. Tempore quidem, amet ex minima atque, nostrum nulla aliquid numquam, exercitationem a sint.</p>';


function App() {
  const editorRef = React.useRef<any>(null)
  const [content, setContent] = React.useState(() => {
    let savedContent = localStorage.getItem('content')
    return  savedContent || sampleContent;
  })

  function onClick() {
    const captureContent = editorRef?.current?.editor?.getContent();
    localStorage.setItem('content', captureContent);
    setContent(captureContent);
  }

  function onClear() {
    setContent("")
    editorRef?.current?.editor?.setContent(" ")
    localStorage.setItem('content', content);
  }

  function addSampleContent() {
    editorRef?.current?.editor?.setContent(sampleContent)
  }

  return (
    <>
    <div style={{ margin: '20px'}}>Rooster JS Editor</div>
    <div className="App">
        <ConnectEditor id="abc" readonly={false} onChange={() => {}} ref={editorRef} content={content} />
    </div>
    <button style={{ marginLeft: '14px' }} onClick={addSampleContent}>Add Sample Content</button>
    <button style={{ margin: '14px' }} onClick={onClick}>Save</button>
    <button style={{ margin: '8px' }} onClick={onClear}>Clear</button>
    </>
  );
}

export default App;
