        // New Design for Non Select dropdown starts for Font Family
        let fontFamilySelCreate = roosterWrap.create('div', { className: 'dropdown-align' });
        fontFamilySelCreate.attr('style', 'margin-bottom: 10px;');
        elem.append(fontFamilySelCreate);

        //Create Hyperlink A Element
        let fontFamilyMainSubAnchorCreate = createToolbarButton('fontFamily', formId[1]);
        fontFamilyMainSubAnchorCreate.on('click', function (event) {
            //document.getElementById("myFontFamilyDDdiv-" + formId[1]).classList.toggle("roosterwrap-show");
            closeAllOtherRoosterDropDowns(event, 1, 'myFontFamilyDDdiv-' + formId[1], 'roosterParent-' + formId[1]);
        });
        fontFamilyMainSubAnchorCreate.on('mousedown', function (event) {
            preventCursorLoss(event);
        });
        //Accessibility Begins
        fontFamilyMainSubAnchorCreate.on('keyup', function (e) {
            const keycode = e.keyCode ? e.keyCode : e.which;

            if (keycode === 13) {
                closeAllOtherRoosterDropDowns(e, 1, 'myFontFamilyDDdiv-' + formId[1], 'roosterParent-' + formId[1]);
                document
                    .getElementById('myFontFamilyDDdivUL-' + formId[1])
                    .children(':first')
                    .focus();
            } else {
                return true;
            }
        });
        //Accessibility Ends
        fontFamilySelCreate.append(fontFamilyMainSubAnchorCreate);

        //Create Dropdown Div Element
        let fontFamilyMainSubDivCreate = roosterWrap.create('div', {
            className: 'dropdown-rooster-common-styles dropdown-rooster-font-family-div-content'
        });
        fontFamilyMainSubDivCreate.attr('id', 'myFontFamilyDDdiv-' + formId[1]);
        fontFamilySelCreate.append(fontFamilyMainSubDivCreate);

        //Create UL under Dropdpwn Div Element
        let fontFamilyMainSubDivULCreate = roosterWrap.create('ul', { className: 'dropdown-rooster-font-family-div-ul-content' });
