function createToolbarButton(buttonName, editorId, editor, addEvents) {
    let roosterWrap = window.roosterWrap;
    let toolbarElement = roosterWrap.create('a', { className: 'just-img-padding' });
    toolbarElement.attr('id', toolBarButtonConfig[buttonName].id + '-' + editorId);
    toolbarElement.attr('title', toolBarButtonConfig[buttonName].title);
    toolbarElement.attr('tabIndex', '0');
    toolbarElement.attr('role', 'button');
    toolbarElement.attr('aria-label', toolBarButtonConfig[buttonName].title);

    createIconInElement(toolbarElement, toolBarButtonConfig[buttonName].iconName);

    if (addEvents) {
        //Accessibility Starts
        toolbarElement.on('keyup', function (e) {
            const keycode = e.keyCode ? e.keyCode : e.which;
            if (keycode === 13) {
                const buttonConfig = toolBarButtonConfig[buttonName];
                buttonConfig.handle(editor);
                toolBarButtonConfig[buttonName].highlightSelection ?? this.classList.toggle('just-image-pad-selection');
                document.getElementById(toolBarButtonConfig[buttonName].id + '-' + editorId).focus();
            } else {
                return true;
            }
        });
        //Accessibility Ends
        toolbarElement.on('click', function () {
            const buttonConfig = toolBarButtonConfig[buttonName];
            buttonConfig.handle(editor);
            if (toolBarButtonConfig[buttonName].highlightSelection) this.classList.toggle('just-image-pad-selection');
        });
    }

    return toolbarElement;
}

function preventCursorLoss(e) {
    e.preventDefault();
}

function setColumnValueOnKeyUp(editor, value, keyCode, type) {
    if (keyCode === 13) {
        setElementColor(editor, value, type);
    } else {
        return true;
    }
}

function insertRoosterHyperlink(editor, val, formID) {
    let dropdowns = document.getElementsByClassName('dropdown-rooster-hyperlink-content');
    let i;
    for (i = 0; i < dropdowns.length; i++) {
        checkAndRemoveShowClass(dropdowns[i]);
    }

    //document.getElementById('contentDiv').focus();
    if (val === 2) {
        return;
    }

    let link = document.getElementById('rowvalofhyperlink-' + formID).value;
    let displayText = document.getElementById('colvalofhyperlink-' + formID).value;

    if (link) {
        roosterjs.createLink(editor, link, '', displayText ? displayText : '');
        // Flushing the values for the dropdown input fields once the link has been successfully created
        document.getElementById('colvalofhyperlink-' + formID).value = '';
        document.getElementById('rowvalofhyperlink-' + formID).value = '';
    }
}

function insertRoosterTable(editor, formID) {
    let dropdowns = document.getElementsByClassName('dropdown-rooster-table-content');
    let i;
    for (i = 0; i < dropdowns.length; i++) {
        checkAndRemoveShowClass(dropdowns[i]);
    }

    //document.getElementById('contentDiv').focus();

    let row = document.getElementById('rowval-' + formID).value;
    let col = document.getElementById('colval-' + formID).value;

    if (row && col) {
        row = parseInt(row);
        col = parseInt(col);
        roosterjs.insertTable(editor, col, row);
        // Flushing the values for the dropdown input fields once the table has been successfully created
        document.getElementById('rowval-' + formID).value = '';
        document.getElementById('colval-' + formID).value = '';
    }
}

// image mime types

function handleImage(editor, formId = undefined){
    var inputElement = document.createElement("input");
    inputElement.type = "file";
    inputElement.accept = [
        'image/bmp',
        'image/gif',
        'image/jpeg',
        'image/png',
        'image/svg+xml',
        'image/tiff',
        'image/webp',
    ];

    inputElement.click();

    // set onchange event to call callback when user has selected file
    inputElement.addEventListener("change", function () {
        let file = inputElement.files[0];
        if (file) {
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = function (e) {
                let dataURL = e.target.result;
                roosterjs.insertImage(editor, dataURL);
                inputElement.remove();
            };
        }
    })
}

function formatState(editor, formID) {
    let formatVals = roosterjs.getFormatState(editor);

    updateFormatForElement(formID, 'buttonB', formatVals.isBold);
    updateFormatForElement(formID, 'buttonI', formatVals.isItalic);
    updateFormatForElement(formID, 'buttonU', formatVals.isUnderline);
    updateFormatForElement(formID, 'buttonUndo', formatVals.canUndo);
    updateFormatForElement(formID, 'buttonNumber', formatVals.isNumbering);
    updateFormatForElement(formID, 'buttonL', formatVals.isBullet);
    updateFormatForElement(formID, 'buttonCL', formatVals.canUnlink);
    updateFormatForElement(formID, 'buttonCA', formatVals.isCenterJustify);
    updateFormatForElement(formID, 'buttonRA', formatVals.isRightJustify);
    updateFormatForElement(formID, 'buttonLA', formatVals.isLeftJustify);
}

function updateFormatForElement(editorId, elementPrefix, isAddFormat) {
    let elementToFormat = document.getElementById(elementPrefix + '-' + editorId);
    if (elementToFormat) {
        if (isAddFormat) elementToFormat.className += ' just-image-pad-selection';
        else elementToFormat.classList.remove('just-image-pad-selection');
    }
}

function setupColorPaletteDropdown(roosterWrap, parentElement, editor, type) {
    Object.keys(colorPaletteOptions).map((rowKey) => {
        let rowElement = roosterWrap.create('div', { className: 'line-col' });
        parentElement.append(rowElement);
        const columnOptions = colorPaletteOptions[rowKey];
        Object.keys(columnOptions).map((columnKey) => {
            let columnElement = roosterWrap.create('div', { className: 'cbd-color-dim cbd-style-' + columnOptions[columnKey].className });
            columnElement.attr('title', columnOptions[columnKey].title);
            columnElement.attr('tabIndex', '0');
            columnElement.attr('role', 'listbox ');
            columnElement.attr('aria-label', columnOptions[columnKey].title);
            columnElement.on('mousedown', function (event) {
                preventCursorLoss(event);
            });
            //Accessibility Begins
            columnElement.on('keyup', function (e) {
                setColumnValueOnKeyUp(editor, columnOptions[columnKey].color, e.keyCode ? e.keyCode : e.which, type);
            });
            //Accessibility Ends
            columnElement.on('click', function () {
                setElementColor(editor, columnOptions[columnKey].color, type);
            });
            rowElement.append(columnElement);
        });
    });
}

function setElementColor(editor, val, type) {
    let dropdowns = document.getElementsByClassName('dropdown-rooster-content');
    let i;
    for (i = 0; i < dropdowns.length; i++) {
        checkAndRemoveShowClass(dropdowns[i]);
    }

    type === 'text' ? roosterjs.setTextColor(editor, val) : roosterjs.setBackgroundColor(editor, val);
}

