function closeAllOtherRoosterDropDowns(e, num, elementId, elementParentId) {
    let dropdowns = document.getElementById(elementParentId).getElementsByClassName('dropdown-rooster-common-styles');
    // if (dropdowns.length == 4) {
    //     num = num - 2;
    // }
    let i;
    for (i = 0; i < dropdowns.length; i++) {
        //console.log(dropdowns[i].id);
        //if (i + 1 != num) {
        if (elementId !== dropdowns[i].id) {
            checkAndRemoveShowClass(dropdowns[i]);
        }
    }
    document.getElementById(elementId).classList.toggle('roosterwrap-show');
    e.stopPropagation();
}

//Closing all dropdowns except current one starts
window.onclick = function (event) {
    let dropdowns = document.getElementsByClassName('dropdown-rooster-common-styles');
    //console.log('Inside Window Rooster Click: ', dropdowns.length);
    let i;
    for (i = 0; i < dropdowns.length; i++) {
        checkAndRemoveShowClass(dropdowns[i]);
    }
};
//Closing all dropdowns except current one ends

function insertRoosterFontStyling(editor, num, formID, val) {
    let dropdowns;
    if (num === 1) {
        dropdowns = document.getElementsByClassName('dropdown-rooster-font-family-div-content');
    } else {
        dropdowns = document.getElementsByClassName('dropdown-rooster-font-size-div-content');
    }

    for (let i = 0; i < dropdowns.length; i++) {
        checkAndRemoveShowClass(dropdowns[i]);
    }

    if (num === 1) {
        roosterjs.setFontName(editor, val);
    } else {
        roosterjs.setFontSize(editor, val + 'pt');
    }
}

function checkAndRemoveShowClass(dropdownElement) {
    if (dropdownElement.classList.contains('roosterwrap-show')) {
        dropdownElement.classList.remove('roosterwrap-show');
    }
}

function createIconInElement(element, iconName) {
    let roosterWrap = window.roosterWrap;
    let iconParentElement = roosterWrap.create('span', { className: 'just-img just-img-common-second' });
    element.append(iconParentElement);

    //Create Nested Undo under Span of Undo Element
    let iconElement = roosterWrap.create('i', { className: 'ms-Icon ms-Icon--' + iconName });
    iconParentElement.append(iconElement);
    iconElement.attr('aria-hidden', 'true');
}

